import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import './Home.css';

export default function Home() {
  const navigate = useNavigate();
  const [showAdminLink, setShowAdminLink] = useState(false);
  const [password, setPassword] = useState('');
  const [passwordError, setPasswordError] = useState('');

  // Gestion de l'affichage du modal d'authentification admin
  const [showModal, setShowModal] = useState(false);

  const handleAdminClick = () => {
    setShowModal(true);
  };

  const closeModal = () => {
    setShowModal(false);
    setPassword('');
    setPasswordError('');
  };

  const checkPassword = () => {
    // Mot de passe simple stocké côté frontend uniquement (pour démonstration)
    // Dans un environnement réel, utilisez une meilleure méthode de sécurité
    if (password === 'beyrede2025') {
      navigate('/admin/non-members');
      closeModal();
    } else {
      setPasswordError('Mot de passe incorrect');
    }
  };

  return (
    <div className="home-container">
      <h1>Bienvenue au Club d'Escalade</h1>
      
      <div className="buttons-container">
        <button 
          className="main-button member-button"
          onClick={() => navigate('/member-check')}
        >
          Je suis membre du club
        </button>
        
        <button 
          className="main-button non-member-button"
          onClick={() => navigate('/non-member')}
        >
          Je ne suis pas membre du club
        </button>
      </div>
      
      <div className="admin-link-container">
        <button 
          className="admin-link"
          onClick={handleAdminClick}
        >
          Accès administration
        </button>
      </div>

      {/* Modal d'authentification admin */}
      {showModal && (
        <div className="admin-modal-overlay">
          <div className="admin-modal">
            <h2>Authentification</h2>
            <p>Veuillez entrer le mot de passe administrateur</p>
            
            <input 
              type="password" 
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              placeholder="Mot de passe"
            />
            
            {passwordError && <p className="password-error">{passwordError}</p>}
            
            <div className="modal-buttons">
              <button onClick={closeModal}>Annuler</button>
              <button onClick={checkPassword}>Confirmer</button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
